import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/Layout"
import Seo from "../components/Seo"

const IndexPage = () => (
  <Layout>
    <Seo title="Planner & Developer" />
    <section id="hero" class="block block-hero">
      <h1 class="block-title inner-sm">Welcome</h1>
      <div class="block-content inner-sm">
        <p>어서오세요. KimPD의 디지털 공작실 입니다.</p>
      </div>
    </section>
    <section id="about" class="block block-text">
      <h2 class="block-title underline inner-sm">About</h2>
      <div class="block-content inner-sm">
        <p>This is the &quot;about&quot; excerpt. It can be used to provide a paragraph about yourself that people can read on the homepage to get a sense of who you are. There also exists a dedicated about page where you can write more about yourself for those who are interested.</p>
      </div>
      <div class="block-buttons inner-sm">
        <a href="/demos/fresh/contact" class="button">Contact Me</a>
      </div>
    </section>
    <section id="recent-posts" class="block block-posts">
      <h2 class="block-title underline inner-sm">Recent Posts</h2>
      <div class="post-feed">
        <div class="post-feed-inside">
          <article class="post post-card">
            <div class="post-inside">
              <a class="post-thumbnail" href="/demos/fresh/posts/fragments-of-iceland"><img src="images/7.jpg" alt="Icelandic horses" /></a>
              <header class="post-header">
                <h3 class="post-title"><a href="/demos/fresh/posts/fragments-of-iceland" rel="bookmark">Fragments of Iceland</a></h3>
              </header>
              <div class="post-content">
                <p>Iceland is a Nordic country between the North Atlantic and the Arctic Ocean. It has a population of 325,671 and an area of 103,000 km2 (40,000 sq mi), making it the most sparsely populated country in Europe.</p>
              </div>
              <footer class="post-meta">
                <time class="published" datetime="2019-03-27 00:00">March 27, 2019</time>
              </footer>
            </div>
          </article>
          <article class="post post-card">
            <div class="post-inside">
              <a class="post-thumbnail" href="/demos/fresh/posts/japan"><img src="images/2.jpg" alt="Fushimi Inari Shrine in Kyoto" /></a>
              <header class="post-header">
                <h3 class="post-title"><a href="/demos/fresh/posts/japan" rel="bookmark">Minimalist Travel - A Weekend in Japan</a></h3>
              </header>
              <div class="post-content">
                <p>The Shrines of Ise have been celebrated as the prototype of Japanese architecture. Largely of wood, traditional housing and many temple buildings see the use of tatami mats and sliding doors that break down the distinction between rooms and indoor and outdoor space.</p>
              </div>
              <footer class="post-meta">
                <time class="published" datetime="2019-03-24 00:00">March 24, 2019</time>
              </footer>
            </div>
          </article>
          <article class="post post-card">
            <div class="post-inside">
              <a class="post-thumbnail" href="/demos/fresh/posts/grand-canyon"><img src="images/8.jpg" alt="Hikers on the trail" /></a>
              <header class="post-header">
                <h3 class="post-title"><a href="/demos/fresh/posts/grand-canyon" rel="bookmark">Hiking The Grand Canyon</a></h3>
              </header>
              <div class="post-content">
                <p>The Grand Canyon is a steep-sided canyon carved by the Colorado River in Arizona, United States. The Grand Canyon is 277 miles (446 km) long, up to 18 miles (29 km) wide and attains a depth of over a mile (6,093 feet or 1,857 meters).</p>
              </div>
              <footer class="post-meta">
                <time class="published" datetime="2019-03-10 00:00">March 10, 2019</time>
              </footer>
            </div>
          </article>
          <article class="post post-card">
            <div class="post-inside">
              <a class="post-thumbnail" href="/demos/fresh/posts/the-ride-in-the-woods"><img src="images/3.jpg" alt="A motorcycle in a forest" /></a>
              <header class="post-header">
                <h3 class="post-title"><a href="/demos/fresh/posts/the-ride-in-the-woods" rel="bookmark">The Ride In The Woods</a></h3>
              </header>
              <div class="post-content">
                <p>The Ride In The Woods lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
              </div>
              <footer class="post-meta">
                <time class="published" datetime="2018-01-23 00:00">January 23, 2018</time>
              </footer>
            </div>
          </article>
        </div>
      </div>
      <div class="block-buttons inner-sm">
        <a href="blog/index.html" class="button">View Blog</a>
      </div>
    </section>
  </Layout>
)

export default IndexPage
